import { useCallback, useEffect, useRef, useState } from 'react';
import * as tf from '@tensorflow/tfjs';
import * as cocossd from '@tensorflow-models/coco-ssd';
import { Detection } from '../types/detection';

export const useObjectDetection = () => {
  const [isModelLoading, setIsModelLoading] = useState(true);
  const [model, setModel] = useState<cocossd.ObjectDetection | null>(null);
  const [detections, setDetections] = useState<Detection[]>([]);
  const [error, setError] = useState<string | null>(null);

  // Initialize TensorFlow.js and load COCO-SSD model
  useEffect(() => {
    const loadModel = async () => {
      try {
        await tf.ready();
        const loadedModel = await cocossd.load();
        setModel(loadedModel);
        setIsModelLoading(false);
      } catch (err) {
        setError('Failed to load object detection model');
        setIsModelLoading(false);
      }
    };

    loadModel();
  }, []);

  // Detect objects in video frame
  const detectObjects = useCallback(async (video: HTMLVideoElement) => {
    if (!model) return;

    try {
      const predictions = await model.detect(video);
      const timestamp = new Date().toISOString();
      
      const detectedObjects = predictions.map(prediction => ({
        bbox: prediction.bbox as [number, number, number, number],
        class: prediction.class,
        score: prediction.score,
        timestamp
      }));

      setDetections(prev => [...prev, ...detectedObjects]);
    } catch (err) {
      setError('Error detecting objects');
    }
  }, [model]);

  const clearDetections = useCallback(() => {
    setDetections([]);
  }, []);

  return {
    isModelLoading,
    detectObjects,
    detections,
    clearDetections,
    error
  };
};
