'use client';

import React, { useRef, useEffect, useState } from 'react';
import { useObjectDetection } from '../hooks/use-object-detection';
import { Card } from './ui/card';
import { Button } from './ui/button';
import { ScrollArea } from './ui/scroll-area';
import { Alert, AlertDescription } from './ui/alert';
import { AlertCircle } from 'lucide-react';

export const WebcamDetector = () => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [isWebcamActive, setIsWebcamActive] = useState(false);
  const [stream, setStream] = useState<MediaStream | null>(null);
  const [webcamError, setWebcamError] = useState<string | null>(null);
  const { isModelLoading, detectObjects, detections, clearDetections, error } = useObjectDetection();

  // Start webcam
  const startWebcam = async () => {
    try {
      setWebcamError(null);
      const mediaStream = await navigator.mediaDevices.getUserMedia({
        video: { 
          facingMode: 'environment',
          width: { ideal: 1280 },
          height: { ideal: 720 }
        }
      });
      
      if (videoRef.current) {
        videoRef.current.srcObject = mediaStream;
        await videoRef.current.play();
        setStream(mediaStream);
        setIsWebcamActive(true);
      }
    } catch (err) {
      console.error('Error accessing webcam:', err);
      let errorMessage = 'Failed to access webcam';
      
      if (err instanceof Error) {
        if (err.name === 'NotAllowedError' || err.name === 'PermissionDeniedError') {
          errorMessage = 'Camera access was denied. Please grant permission to use your camera.';
        } else if (err.name === 'NotFoundError' || err.name === 'DevicesNotFoundError') {
          errorMessage = 'No camera device was found on your system.';
        } else if (err.name === 'NotReadableError' || err.name === 'TrackStartError') {
          errorMessage = 'Your camera is already in use by another application.';
        }
      }
      
      setWebcamError(errorMessage);
    }
  };

  // Stop webcam
  const stopWebcam = () => {
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
      setStream(null);
      setIsWebcamActive(false);
      if (videoRef.current) {
        videoRef.current.srcObject = null;
      }
    }
  };

  // Run object detection on video frames
  useEffect(() => {
    if (!isWebcamActive || !videoRef.current || isModelLoading) return;

    const video = videoRef.current;
    let animationFrameId: number;

    const detectFrame = async () => {
      if (video.readyState === video.HAVE_ENOUGH_DATA) {
        await detectObjects(video);
      }
      animationFrameId = requestAnimationFrame(detectFrame);
    };

    video.onloadedmetadata = () => {
      video.play();
      detectFrame();
    };

    return () => {
      cancelAnimationFrame(animationFrameId);
    };
  }, [isWebcamActive, isModelLoading, detectObjects]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [stream]);

  return (
    <div className="container mx-auto p-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Webcam Feed */}
        <Card className="p-4">
          <div className="relative aspect-video bg-black rounded-lg overflow-hidden">
            <video
              ref={videoRef}
              className="absolute inset-0 w-full h-full object-cover"
              playsInline
            />
            {isModelLoading && (
              <div className="absolute inset-0 flex items-center justify-center text-white bg-black/50">
                Loading TensorFlow model...
              </div>
            )}
          </div>
          <div className="mt-4 space-y-4">
            <div className="flex gap-2">
              <Button
                onClick={isWebcamActive ? stopWebcam : startWebcam}
                variant={isWebcamActive ? "destructive" : "default"}
              >
                {isWebcamActive ? 'Stop Camera' : 'Start Camera'}
              </Button>
              <Button onClick={clearDetections} variant="outline">
                Clear Log
              </Button>
            </div>
            
            {webcamError && (
              <Alert variant="destructive">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>{webcamError}</AlertDescription>
              </Alert>
            )}
          </div>
        </Card>

        {/* Detection Log */}
        <Card className="p-4">
          <h2 className="text-xl font-bold mb-4">Detection Log</h2>
          <ScrollArea className="h-[400px]">
            {detections.length === 0 ? (
              <p className="text-muted-foreground">No objects detected yet</p>
            ) : (
              <div className="space-y-2">
                {detections.map((detection, index) => (
                  <div
                    key={`${detection.timestamp}-${index}`}
                    className="p-2 border rounded-lg"
                  >
                    <p className="font-medium">{detection.class}</p>
                    <p className="text-sm text-muted-foreground">
                      Confidence: {(detection.score * 100).toFixed(2)}%
                    </p>
                    <p className="text-xs text-muted-foreground">
                      {new Date(detection.timestamp).toLocaleString()}
                    </p>
                  </div>
                ))}
              </div>
            )}
          </ScrollArea>
          {error && (
            <Alert variant="destructive" className="mt-4">
              <AlertCircle className="h-4 w-4" />
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </Card>
      </div>
    </div>
  );
};
